.list{
  background-color:	#9999FF;
  color:white;
  height:50px;
  width: 500px;
  border-radius: 5px;
  margin: 30px 20px;
  font-weight:20px;

}

.list p{
  padding:10px;
  font-size: 25px;
  color: black;
  position: relative;

}
.list p span{
  position:absolute;
  right: 15px;
  cursor:pointer;
}

.list p input{
  background-color: transparent;
  border:0px;
  font-size: 20px;
}

.list p input:focus {
  outline: none;
}
