body{
  background-color: #006666;
}

.App{
  background-color: #606060;
  min-height: 500px;
  width: 800px;
  margin: 50px auto;
  padding: 10px;
  border-radius:10px;


}

#to-do-form input{
  background-color: #66FFB2;
  border:0px;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;


}

#to-do-form input:focus {
  outline: none;
}
#to-do-form input::placeholder{
  color: white;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  background-color: rgb(202, 156,24);
  cursor: pointer;
  font-size: 18px;
  border: 0px;
  margin: -10px;

}

.title{
  color: #FF3399;
  margin: 20px 100px;
}
